import React from 'react';
import { Link } from "gatsby"

import Navigator from "../components/Navigator"

import * as styles from "../styles/Post.module.css";

/** Template component for post list.
    Use 'pageContext' to fetch the list of postings. */
export default function PostList({ pageContext }) {
  return (
    <div className={styles["post"]}>
      <Navigator />
      <div className={styles["post__content"]}>
        {pageContext.postList.map((post) => (
          <div>
            <Link to={`${process.env.GATSBY_PUBLIC_URL}/${post.postLink}`}>{`${process.env.GATSBY_PUBLIC_URL}/${post.postLink}`}</Link>
          </div>
        ))}
      </div>
    </div>
  );
}
